<template>
  <div class="partner-company">
    <el-container>
      <el-header class="header">
        <!-- 搜索表单 start -->
        <el-form
          :inline="true"
          :model="searchValidateForm"
          ref="searchValidateForm"
          label-width="100px"
          class="search-validate-form"
          :rules="searchValidateFormRules"
        >

          <!-- 请选择景区 start -->
         <el-form-item label="" prop="scenicSpotName">
			<el-select
			  v-model="searchValidateForm.scenicSpotId"
			  filterable
			  placeholder="请选择景区"
			  clearable
			>
			  <el-option
			    v-for="(item, index) in spotList"
			    :key="item.value"
			    :label="item.scenicSpotName"
			    :value="item.scenicSpotId"
			  >
			  </el-option>
			</el-select>
            </el-input>
          </el-form-item>
          <!-- 请选择景区 end -->

          <!-- 请输入手机号 start -->
         <el-form-item label="" prop="userPhone">
			<el-input v-model="searchValidateForm.userPhone1" placeholder="请输入手机号" />
          </el-form-item>
          <!-- 请输入手机号 end -->
		  
		  <!-- 请输入奖品名称 start -->
		  <el-form-item label="" prop="treasureLogName">
		  			<el-input v-model="searchValidateForm.treasureLogName" placeholder="请输入奖品名称" />
		   </el-form-item>
		   <!-- 请输入奖品名称 end -->
		   
		   <!-- 请选择奖品类型 start -->
		   <el-form-item label="" prop="treasureLogType">
		   			<el-select
		   			  v-model="searchValidateForm.treasureLogType"
		   			  filterable
		   			  placeholder="请选择奖品类型"
		   			  clearable
		   			>
		   			  <el-option
		   			    v-for="(item, index) in treasureLogTypeList"
		   			    :key="item.value"
		   			    :label="item.label"
		   			    :value="item.value"
		   			  >
		   			  </el-option>
		   			</el-select>
		      </el-input>
		    </el-form-item>
		    <!-- 请选择奖品类型 end -->
			
			<!-- 请选择奖品规格 start -->
			<el-form-item label="" prop="prizeSize">
						<el-select
						  v-model="searchValidateForm.prizeSize"
						  filterable
						  placeholder="请选择奖品规格"
						  clearable
						>
						  <el-option
						    v-for="(item, index) in prizeSizeList"
						    :key="item.value"
						    :label="item.label"
						    :value="item.value"
						  >
						  </el-option>
						</el-select>
			   </el-input>
			 </el-form-item>
			 <!-- 请选择奖品规格 end -->
		   
        </el-form>
        <!-- 搜索表单 end -->

        <!-- 查询数据 start -->
        <el-button
          type="primary"
          icon="el-icon-search"
          @click="onSearch('searchValidateForm')"
          >搜索</el-button
        >
        <!-- 查询数据 end -->

        <!-- 重置 start -->
        <el-button type="primary" icon="el-icon-refresh-right" @click="onReset"
          >重置</el-button
        >
        <!-- 重置 end -->
      </el-header>

      <el-main>
        <!-- 表格 start -->
        <el-table
          class="table"
          :data="tableData"
          style="width: 100%"
          border
          v-loading="tableLoading"
          :header-cell-style="{
            background: '#f2f2f2',
            color: '#606266',
            height: '38px',
            padding: '0px'
          }"
          :cell-style="{ height: '38px', padding: '0' }"
        >
          <el-table-column
            v-for="(value, key) in tableFields"
            :key="key"
            :prop="value"
            :label="key"
            min-width="130"
          >
            <template slot-scope="scope">
              <div>
                {{ tableContentValue(scope.row, value) }}
              </div>
            </template>
          </el-table-column>
        </el-table>
        <!-- 表格 end -->

        <!-- 导出的表格 start -->
        <!-- 此表格始终保持隐藏 -->
        <el-table
          id="outTable"
          class="table"
          :data="exportTableData"
          style="width: 100%; display: none"
          border
        >
          <el-table-column
            v-for="(value, key) in tableFields"
            :key="key"
            :prop="value"
            :label="key"
            min-width="130"
            align="center"
          >
          </el-table-column>
        </el-table>
        <!-- 导出的表格 end -->
      </el-main>

      <el-footer>
        <!-- 分页 start -->
        <div class="block">
          <el-pagination
            background
            layout="prev, pager, next, total, sizes, jumper"
            :total="total"
            :current-page="currentPage"
            :page-sizes="[10, 20, 30, 40]"
            :page-size="pageSize"
            @current-change="onCurrentChange"
            @size-change="onSizeChange"
          >
          </el-pagination>
        </div>
        <!-- 分页 end -->
      </el-footer>
	
    </el-container>
  </div>
</template>

<script>
// import JsonExcel from 'vue-json-excel'
import FileSaver from 'file-saver'
import XLSX from 'xlsx'

import { createUrl,formatDate } from '../utils/utils.js'
export default {
  name: 'SettlementFlow',
  data() {
    /**
     * 公共验证函数(验证数字值)
     * @params message - 提示信息
     */
    function checkNumberMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          // 如果值为空，可以回调
          callback()
        }

        if (isNaN(Number(value))) {
          callback(new Error(name + '必须为数字值'))
        } else {
          callback()
        }
      }
    }

    /**
     * 公共验证函数(验证必填数字值)
     * @params message - 提示信息
     */
    function checkRequireNumberMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'))
        }

        if (isNaN(Number(value))) {
          callback(new Error(name + '必须为数字值'))
        } else {
          callback()
        }
      }
    }

    /**
     * 公共验证函数(验证必填值)
     * @params message - 提示信息
     */
    function checkRequireMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'))
        } else {
          callback()
        }
      }
    }

    return {
      searchCompanyOptions: [], // 搜索的公司选项
      searchScenicOptions: [], // 搜索的景区选项
      receivingOptions: [], // 收货景区列表

      searchInStockTypeOptions: [
        {
          value: '1',
          label: '采购'
        },
        {
          value: '2',
          label: '回收'
        }
      ], // 入库类型选项

      dateStateOptions: [
        {
          value: '1',
          label: '按月查询'
        },
        {
          value: '2',
          label: '按日查询'
        },
        {
          value: '3',
          label: '按年查询'
        }
      ], // 日期统计方式选项
      searchSpotNameOptions: [], // 搜索时供货商选项
      searchGoodsNameOptions: [], // 搜索时配件名称选项

      spotIdOptions: [], // 景区选项列表

      // 快捷选项配置
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now()
        },
        shortcuts: [
          {
            text: '今天',
            onClick(picker) {
              picker.$emit('pick', new Date())
            }
          },
          {
            text: '昨天',
            onClick(picker) {
              const date = new Date()
              date.setTime(date.getTime() - 3600 * 1000 * 24)
              picker.$emit('pick', date)
            }
          },
          {
            text: '一周前',
            onClick(picker) {
              const date = new Date()
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7)
              picker.$emit('pick', date)
            }
          }
        ]
      },

      exportTableData: [], // 导出的全部表格数据
      downloadLoading: false, // 导出excel按钮的加载动画
      importLoading: false, // 导入excel按钮的加载动画

      currentPage: 1, // 当前页数,
      pageSize: 10, // 每页显示条数

      selectDateState: '1', // 日期统计方式（月，日，年）
      dateType: 'daterange', // 日期类型（月，日，年）
      valueFormat: 'yyyy-MM-dd', // 日期格式

      searchValidateForm: {}, // 搜索表单验证对象
      editValidateForm: {}, // 表单验证对象
      previewValidateForm: {
        previewOrderNumber: '' // 预览单号
      }, // 预览表单对象

      searchPartnerCompanyName: '', // 搜索的公司
      searchScenicName: '', // 搜索的景区
      searchSelectDateState: '1', // 搜索的日期统计方式
      searchStartEndDate: '', // 搜索的起止时间
      searchPayState: '', // 搜索的支付状态

      companys: [], // 公司列表
      companyState: '', // 当前请选择公司

      scenics: [], // 景区列表
      scenicState: '', // 当前请选择景区
	  oddNumberList:[],//单号

      downloadUrl: '',

      total: 0,

      operateStatus: 0, // 操作状态（0、初始状态；1、确认；2、撤销）

      dialogVisible: false,

      dialogState: '', // 当前dialog弹窗是新增还是编辑
      currentRowOrderNumber: '', // 当前行单号

      // previewOrderNumber: "", // 预览单号

      // 搜索表单规则
      searchValidateFormRules: {},
      // 预览表单规则
      previewValidateFormRules: {
        // 单号
        previewOrderNumber: [
          {
            validator: checkRequireMethod('单号'),
            required: true,
            trigger: ['blur', 'change']
          }
        ]
      },

      // 提交表单规则
      validateFormRules: {
        // // 机器人数量
        // robotCount: [
        //   { validator: checkNumberMethod('机器人数量'), trigger: ['blur', 'change'] }
        // ],
      },

      // 提交编辑表单规则
      editValidateFormRules: {},
	  //新增
	  showTable: true, // 是否显示表格
	    tableFields: {
	  		  
	  		  景区名称:'scenicSpotName',
	  		  用户手机号:'userPhone1',
	  		  奖品名称:'treasureLogName',
	  		  奖品类型:'treasureLogType',
	  		  消耗前积分额度:'frontIntegralNum',
	  		  消耗后积分额度:'afterIntegralNum',
			  当前消费积分:'useIntegralNum',
			  奖品规格:'prizeSize',
			  创建时间:'createDate',
			  更新时间:'updateDate',
	      },
	      tableData: [], // 表格数据
	      tableLoading: false,
	  spotList:[],
	  treasureLogTypeList:[
		  {
		    value: '1',
		    label: '代金卷',
		  },
		  {
		    value: '2',
		    label: '免单',
		  },
		  {
		    value: '3',
		    label: '实物',
		  },
		  {
		    value: '4',
		    label: '谢谢参与',
		  },
	  ],
	  prizeSizeList:[
		  {
		    value: '0',
		    label: '普通奖品',
		  },
		  {
		    value: '1',
		    label: '大奖',
		  }
	  ],
	  dialogVisible:false,
	  PrizecurrentPage:1,
	  PrizePageSize:10,
	  drawer:false,
	  direction:'rtl',
	  drawerTitle:'',
	  formSize:'',
	  validateForm: {}, // 表单验证对象
	  drawerPrize:false,
	  drawerTitlePrize:'',
	  validateFormPrize:{},
	  searchValidateFormPrize:{},
	  searchValidateFormRulesPrize:{},
	  prizeData:[],
	  PrizeTotalPage:0,
	  fileList:[],
	  file:{},
	  imgBaseUrl:'https://jxzy-robot.oss-cn-beijing.aliyuncs.com/'
    }
  },
  created() {
    // 如果当前环境是开发环境才调用登录方法
    if (process.env.NODE_ENV == 'development') {
      this.login()
    }

    this.getSearchValidateForm()
	this.getSearchValidateFormPrize()
    this.getValidateForm()
	this.getSpotList()
 //    this.getFactoryFour()
	// this.getoddNumber()
    this.initTableData()
  },
  beforeMount() {},
  mounted() {},
  methods: {
	  // 点击删除时触发
	  deleteRow(index, row) {
	    // console.log(index, row);
	    this.$confirm('此操作将删除该行数据, 是否继续?', '提示', {
	      confirmButtonText: '确定',
	      cancelButtonText: '取消',
	      type: 'warning'
	    })
	      .then(async () => {
	        // let data = this.$qs.stringify({
	        //   id: row.id
	        // })
	        const { data: res } = await this.$http.post(
	          '/system/broadcastHunt/delectJackpotNew',
	          {
	            jackpotId: row.jackpotId
	          }
	        )
	        // console.log(res);
	        if (res.state == '200') {
	          this.$message({
	            type: 'success',
	            message: res.msg,
	            offset: 100,
	            center: true,
	            duration: 1000
	          })
	          this.initTableData()
	        } else {
	          this.$message({
	            type: 'error',
	            message: res.msg,
	            offset: 100,
	            center: true,
	            duration: 3000
	          })
	        }
	      })
	      .catch(() => {
	        this.$message({
	          type: 'info',
	          message: '已取消删除',
	          offset: 100,
	          center: true,
	          duration: 1000
	        })
	      })
	  },
	  cancelClick(){
		  this.drawer = false
		  this.validateForm = {}
	  },
	  cancelClickPrize(){
		  this.drawerPrize = false
		  this.validateFormPrize = {}
	  },
	  async getSpotList(){
		  const { data: res } = await this.$http.get('/system/broadcastHunt/getSpotId')
		  if(res.state == 200){
			  this.spotList = res.data
		  }
		  console.log(res)
	  },
    // 获取搜索表单验证对象
    getSearchValidateForm() {
      var validateForm = {}
      for (var item in this.tableFields) {
        validateForm[this.tableFields[item]] = ''
      }
      this.searchValidateForm = {
        ...validateForm,
        goodId: null,
        spotId: null,
        inStockReason: ''
      }
    },
	
	getSearchValidateFormPrize() {
	  var validateForm = {}
	  for (var item in this.tableFields) {
	    validateForm[this.tableFields[item]] = ''
	  }
	  this.searchValidateFormPrize = {}
	},

    // 获取提交表单验证对象
    getValidateForm() {
      var validateForm = {}
      for (var item in this.tableFields) {
        validateForm[this.tableFields[item]] = ''
      }
      this.validateForm = validateForm
      // console.log(this.validateForm);
    },

    // 登录(必须)
    async login() {
      const { data: res } = await this.$http.post(
        '/loginSystem?username=houjingchen&password=houjingchen'
      )
    },

    /**
     * 初始化表格数据
     * @params - isPage - 是否分页
     */
    async initTableData(isPage = false) {
      this.tableLoading = true

	const { data: res } = await this.$http.post(
        '/system/broadcastHunt/getUserIntegralHuntLog',{
			...this.searchValidateForm,
			pageNum: this.currentPage, // 第几页
			pageSize: this.pageSize // 每页多少条
		})
      console.log(res.data);
      if (res.state == '200') {
        // this.tableData = res.data
        // this.total = res.total
		this.tableData = res.data.list
		this.total = res.data.total
        this.tableLoading = false
      }
    },

//查看奖品
	handlePrize(index, row){
		this.jackpotId = row.jackpotId
		this.dialogVisible = !this.dialogVisible
		this.getPrizeList(this.jackpotId)
	},

	async getPrizeList(jackpotId){
		const { data: res } = await this.$http.post(
		  '/system/broadcastHunt/getTreasureNewHunt',{
			  ...this.searchValidateFormPrize,
			  jackpotId:jackpotId,
			  pageNum:this.PrizecurrentPage,
			  pageSize:this.PrizePageSize,
		})
		console.log(res)
		// if(prizeData)
		this.PrizeTotalPage = res.data.total
		this.prizeData = res.data.list
	},
    /**
     * 更改确认/撤销状态
     * @param id - 当前行数据id
     * @param status - 当前行数据status(0、初始状态；1、确认；2、撤销)
     */
    async updateStatus(id, status) {
      const { data: res } = await this.$http.post(
        '/system/inventory_detail/updateStatus',
        this.$qs.stringify({
          id: id,
          status: status // 0、初始状态；1、确认；2、撤销
        })
      )
      this.initTableData(true)
    },

    /**
     * 根据单号查询数据(预览)
     */
    async getByOrderNumber() {
      if (!this.previewValidateForm.previewOrderNumber) {
        this.previewValidateForm.previewOrderNumber = null
      }

      const { data: res } = await this.$http.get(
        '/system/inventory_detail/getByOrderNumber',
        {
          params: {
            orderNumber: this.previewValidateForm.previewOrderNumber,
            type: 1 // 1、入库；2、出库
          }
        }
      )
      return res
    },

    // 点击当前行确认时触发
    confirmRow(index, row) {
      this.$confirm('确认已入库?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          this.updateStatus(row.id, 1)
          this.$message({
            type: 'success',
            message: '确认成功!'
          })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消确认操作'
          })
        })
    },

    // 点击当前行撤销时触发
    revokeRow(index, row) {
      this.$confirm('您确定要撤销吗?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          this.updateStatus(row.id, 2)
          this.$message({
            type: 'success',
            message: '撤销成功!'
          })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消撤销操作'
          })
        })
    },

    /** 提交表单中选项变化方法 start */
    // 申请人选项变化时触发
    async onChangeApplicant(applicantId) {
      var applicantName = ''
      this.applicanteOptions.forEach((item) => {
        if (item.userId == applicantId) {
          applicantName = item.userName
        }
      })
      this.validateForm.applicantName = applicantName
    },

    // 发货工厂选项变化时触发
    async onChangeFactory(factoryId) {
      var factoryName = ''
      this.factoryOptions.forEach((item) => {
        if (item.spotId == factoryId) {
          factoryName = item.spotName
        }
      })
      this.validateForm.factoryName = factoryName
    },

    // 发货人选项变化时触发
    async onChangeConsignor(consignorId) {
      var consignorName = ''
      this.consignorOptions.forEach((item) => {
        if (item.userId == consignorId) {
          consignorName = item.userName
        }
      })
      this.validateForm.consignorName = consignorName
    },

    // 收货景区选项变化时触发
    async onChangeReceiving(receivingId) {
      var receivingName = ''
      var receiningAddress = ''
      this.receivingOptions.forEach((item) => {
        if (item.spotId == receivingId) {
          receivingName = item.spotName
          receiningAddress = item.address
        }
      })
      this.validateForm.receivingName = receivingName
      this.validateForm.receiningAddress = receiningAddress
    },

    // 收货人选项变化时触发
    async onChangeConsignee(consigneeId) {
      var consigneeName = ''
      this.consigneeOptions.forEach((item) => {
        if (item.userId == consigneeId) {
          consigneeName = item.userName
        }
      })
      this.validateForm.consigneeName = consigneeName
    },

    /** 提交表单中选项变化方法 end */

    // 日期选项值发生变化时触发
    onDateChange() {
      console.log(this.selectDateState)
      if (this.selectDateState == '1') {
        // 按月查询
        this.dateType = 'monthrange'
        this.valueFormat = 'yyyy-MM'
      } else if (this.selectDateState == '2') {
        // 按日查询
        this.dateType = 'daterange'
        this.valueFormat = 'yyyy-MM-dd'
      } else if (this.selectDateState == '3') {
        // 按年查询
        this.dateType = 'yearrange'
        this.valueFormat = 'yyyy'
      }
    },

    // 重置
    onReset() {
      // 清空所有输入框内容
      this.getSearchValidateForm()
      this.searchStartEndDate = []
      this.selectDateState = '1'
      this.dateType = 'monthrange'

      // 重新获取表格数据
      this.initTableData()
    },
	onResetPrize(){
		this.getSearchValidateFormPrize()
		this.getPrizeList(this.jackpotId)
	},

    // 点击查询数据按钮时触发
    onSearch(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.showTable = true
          // console.log(this.searchValidateForm);
          this.initTableData()
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
	
	onSearchPrize(formName){
		this.showTable = true
		// console.log(this.searchValidateForm);
		this.getPrizeList(this.jackpotId)
	},


    // 新增
    onAdd() {
		this.drawer = true
		this.drawerTitle = '添加'
		this.validateForm = {}
      // this.dialogVisible = true
      // this.dialogState = 'add'
      // 调用子组件里的 onAddClick 方法
      // this.$nextTick(() => {
      //   this.$refs.stockList.onAddClick('add')
      // })
    },

    // 点击编辑时触发
    editRow(index, row) {
      this.drawer = true
      this.drawerTitle = '编辑'
	  var rowObj = Object.assign({}, row) //
		this.validateForm = rowObj
    },
	

    // 点击详情时触发
    detailRow(index, row) {
      // console.log(index, row);
      this.detailDialogVisible = true
      this.dialogState = 'detail'
      this.detailTableDate(row.id)
    },

    // 新增 dialog 取消
    dialogClose() {
      this.dialogVisible = false
      this.$refs.validateForm.resetFields() // 重置表单（清空无效）
      this.getValidateForm() // 将validateForm对象中各个属性清空
    },

    // 编辑 dialog 取消
    editDialogClose() {
      this.editDialogVisible = false
      this.$refs.editValidateForm.resetFields() // 重置表单（清空无效）
      this.editValidateForm = {}
      // this.getValidateForm() // 将validateForm对象中各个属性清空
    },

    // 详情 dialog 取消
    detailDialogClose() {
      this.detailDialogVisible = false
    },

    // dialog 右上角关闭按钮
    handleClose(done) {
      done() // 关闭dialog对话框
      this.ValidateForm = {}
      // 调用子组件里的 resetFields 方法
      // this.$refs.stockList.resetFields()
      // this.$refs.validateForm.resetFields(); // 重置表单（清空无效）
      // this.getValidateForm() // 将validateForm对象中各个属性清空
    },

	handleClosePrize(done){
		done() 
		this.validateFormPrize = {}
	},

    // 详情 dialog 右上角关闭按钮
    detailHandleClose(done) {
      done()
    },

    // dialog提交
    submitForm(formName) {
		if (this.drawerTitle == '添加') {
		  // 新增工厂发货信息
		  this.addTableData()
		  this.drawer = false
		} else if (this.drawerTitle == '编辑') {
		  // 编辑工厂发货信息
		  this.editTableData()
		  this.drawer = false
		} 
		this.validateForm = {}
    },
	
	
	async addTableData(){
		const { data: res } = await this.$http.post(
		  '/system/broadcastHunt/addJackpotNew',
		  this.validateForm
		)
		if (res.state == '200') {
		  this.$message({
		    message: res.msg,
		    type: 'success',
		    offset: 100,
		    center: true,
		    duration: 1000
		  })
		  this.initTableData()
		} else {
		  this.$message({
		    message: res.msg,
		    type: 'error',
		    offset: 100,
		    center: true,
		    duration: 3000
		  })
		}
	},
	
	async editTableData(){
		const { data: res } = await this.$http.post(
		  '/system/broadcastHunt/updateJackpotNew',
		  this.validateForm
		)
		if (res.state == '200') {
		  this.$message({
		    message: res.msg,
		    type: 'success',
		    offset: 100,
		    center: true,
		    duration: 1000
		  })
		  this.initTableData()
		} else {
		  this.$message({
		    message: res.msg,
		    type: 'error',
		    offset: 100,
		    center: true,
		    duration: 3000
		  })
		}
	},

    // currentPage 改变时会触发
    onCurrentChange(currentPage) {
      this.currentPage = currentPage
      this.initTableData(true)
    },

    // pageSize 改变时会触发
    onSizeChange(pageSize) {
      // console.log(pageSize);
      this.pageSize = pageSize
      this.initTableData(true)
    },
  },
  components: {
    // DownloadExcel: JsonExcel,
    // StockList
  },
  computed: {
    tableContentValue() {
      return (row, value) => {
        // console.log(row, value);
        var result = ''
        if (value == 'treasureLogType') {
          if (row[value] == 1) {
            result = '代金卷'
          } else if (row[value] == 2) {
            result = '免单'
          } else if (row[value] == 3) {
            result = '实物'
          }else{
			  result = '谢谢参与'
		  }
        } else if (value == 'prizeSize') {
          if (row[value] == 0) {
            result = '普通奖品'
          } else if (row[value] == 1) {
            result = '大奖'
          }
        } else {
          result = row[value] || '无'
        }
        return result
      }
    }
  }
}
</script>

